.aboutLayout{display:flex;justify-content:center;margin-top:2rem}.avatarContainer{display:flex;align-items:center;justify-content:space-between;gap:2rem;flex-wrap:wrap;max-width:1200px;margin:0 auto;width:100%}@media(max-width: 768px){.avatarContainer{flex-direction:column;align-items:center}}.avatar{max-width:180px;flex-shrink:0;padding:1rem;border-radius:1rem;transition:transform .3s ease}@media(max-width: 1024px){.avatar{display:none}}.avatar:hover{transform:scale(1.05)}.avatar img{display:block;width:100%;height:auto}.taleContainer{flex:1;display:flex;justify-content:center;min-width:300px}